//   
export default function DrawLine(ctx) {
    //
    this.ctx = ctx || document.querySelector('canvas').getContext("2d");
    this.ctx.strokeStyle = "#80ee1c";// 
    this.gridSize = 5;//
    this.distance = this.gridSize * 5;//  5
    this.canvasWidth = this.ctx.canvas.width;
    this.canvasHeight = this.ctx.canvas.height;
    this.arrowSize = this.gridSize * 2;
    this.dottedSize = this.gridSize * 2;
}
// 
DrawLine.prototype.init = function (data) {
    this.drawGrid();// 
    this.axes();
    this.dotted(data);
};

//  
DrawLine.prototype.drawGrid = function () {

    /*x */
    let xLineTotal = Math.floor(this.canvasHeight / this.gridSize);
    let yLineTotal = Math.floor(this.canvasWidth / this.gridSize);
    // x
    for (let i = 0; i <= xLineTotal; i++) {
        this.ctx.beginPath();// 
        this.ctx.moveTo(0, i * this.gridSize - 0.5);
        this.ctx.lineTo(this.canvasWidth, i * this.gridSize - 0.5);
        this.ctx.stroke();
    }
    // y
    for (let i = 0; i <= yLineTotal; i++) {
        this.ctx.beginPath();// 
        this.ctx.moveTo(i * this.gridSize - 0.5, 0);
        this.ctx.lineTo(i * this.gridSize - 0.5, this.canvasHeight);
        this.ctx.stroke();
    }
};

//  
DrawLine.prototype.axes = function () {
    /*x */
    this.ctx.beginPath();
    this.ctx.moveTo(this.distance, this.canvasHeight - this.distance);// 
    this.ctx.lineTo(this.canvasWidth - this.distance - 0.5, this.canvasHeight - this.distance - 0.5);// x  
    this.ctx.lineTo(this.canvasWidth - this.distance - this.arrowSize, this.canvasHeight - this.distance + this.arrowSize / 2);// 
    this.ctx.lineTo(this.canvasWidth - this.distance - this.arrowSize, this.canvasHeight - this.distance - this.arrowSize / 2);//
    this.ctx.lineTo(this.canvasWidth - this.distance, this.canvasHeight - this.distance);// 
    this.ctx.strokeStyle = "#000";
    this.ctx.stroke();
    this.ctx.fill();

    /*y*/
    this.ctx.beginPath();
    this.ctx.moveTo(this.distance, this.canvasHeight - this.distance);// 
    this.ctx.lineTo(this.distance - 0.5, this.distance - 0.5);// y 
    this.ctx.lineTo(this.distance + this.arrowSize / 2, this.distance + this.arrowSize);// y 
    this.ctx.lineTo(this.distance - this.arrowSize / 2, this.distance + this.arrowSize);// y 
    this.ctx.lineTo(this.distance - 0.5, this.distance - 0.5);// y 
    this.ctx.stroke();
    this.ctx.fill();

};

//    foreach
DrawLine.prototype.dotted = function (data) {
    // this.ctx.moveTo(this.distance,this.canvasHeight-this.distance);// 
    let preDotted = {
        x: this.distance,
        y: this.canvasHeight - this.distance,
    };
    //data point 
    data.forEach((item) => {
        // s
        this.ctx.beginPath();
        this.ctx.moveTo(item.x - this.dottedSize / 2, item.y - this.dottedSize / 2);
        this.ctx.lineTo(item.x + this.dottedSize / 2, item.y - this.dottedSize / 2);
        this.ctx.lineTo(item.x + this.dottedSize / 2, item.y + this.dottedSize / 2);
        this.ctx.lineTo(item.x - this.dottedSize / 2, item.y + this.dottedSize / 2);
        this.ctx.closePath();
        this.ctx.stroke();
        this.ctx.fill();
        this.ctx.beginPath();
        this.ctx.moveTo(preDotted.x, preDotted.y);
        this.ctx.lineTo(item.x, item.y);
        preDotted = {
            x: item.x,
            y: item.y,
        };
        this.ctx.stroke();
    })
};


